.Section {
	padding: 50px;
	@include children-spacing(25px);

	> * {
		margin: 0 auto;
	}

	&__title {
		font-family: $font-main;
		font-weight: 700;
		font-size: $font-big-size;
		line-height: $font-big-height;
		text-align: center;
	}

	&__subtitle {
		font-family: $font-alt;
		text-align: center;
	}
}
